body {
    font-family: 'Open Sans', sans-serif;
    background-color: rgb(66, 66, 66);
    max-width: auto;
    height: 100vh;
    margin-left: 3vh;
}

.nav-bar {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-bottom: 2vh;
    border-radius: 3px;
}

.nav-bar a {
    color: white;
    padding: 10px;
    padding-left: 55px;
    padding-right: 55px;
    background-color: rgb(66, 66, 66);
    border-top: 1px solid black;
    border-left: 1px solid black;
    border-right: 5px solid black;
    border-bottom: 3px solid black;
    border-radius: 3px;
}

.search-bar {
    display: flex;
    justify-content: center;
}

input {
    background-color: rgb(66, 66, 66);
    border: none;
    width: 50vw;
    height: 4vh;
    margin-bottom: 2vh;
    border-radius: 3px;
    border-top: 1px solid black;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 3px solid black;
}

input::placeholder {
    color: rgb(255, 255, 255);
    padding: 15px;
}

.h1-container {
    display: flex;
    justify-content: center;

}

h1 {
    color: white;
    background-color: rgba(66, 66, 66);
    padding: 20px;
    border-radius: 5px;
    font-family: cursive;
    width: fit-content;
    border-top: 1px solid black;
    border-left: 1px solid black;
    border-right: 5px solid black;
    border-bottom: 3px solid black;
    left: 50%;
}

h2 {
    background-color: rgba(66, 66, 66);
    color: white;
    padding: 20px;
    border-radius: 5px;
    font-family: cursive;
    width: fit-content;
    border-top: 1px solid black;
    border-left: 1px solid black;
    border-right: 5px solid black;
    border-bottom: 3px solid black;
}

h3 {
    font-size: 1.5vh;
    color: white;
    margin: 1vh;
    grid-row: 2;
    grid-column: 2;
}

h4 {
    color: white;
    font-size: 2vh;
    margin-top: 0.5vh;
    margin-bottom: 2vh;

}

.mother-container {
    background-color: rgba(66, 66, 66);
    padding: 20px;
    border-radius: 5px;
    margin-right: 4vh;
    border-top: 1px solid black;
    border-left: 1px solid black;
    border-right: 4px solid black;
    border-bottom: 3px solid black;
    
}

.child-container {
    display: grid;
    justify-items: center;
    background-color: rgba(66, 66, 66);
    border: 1px solid #fedccd;
    margin-bottom: 3vh;
    border-top: 1px solid black;
    border-left: 1px solid black;
    border-right: 3px solid black;
    border-bottom: 2px solid black;
}

.child-container a {
    grid-column: 1;
}

.child-container h3 {
    grid-row: 3;
    grid-column: 1;
    padding-bottom: 1vh;
}

.child-container img {
    grid-column: 1;
    grid-row: 1;
    width: 15.1vw;
    height: 22.2vh;
    border: 2px solid black;
}

.title-container {
    display: grid;
    font-family: cursive;
    max-height: fit-content;
}

a {
    max-width: fit-content;
    color: white;
    font-size: 20px;
    font-weight: bold;
    margin: 1vh;
    text-decoration: none;
}

#asian-container {
    grid-row: 2;
    grid-column: 1;
}

#spanish-container {
    grid-row: 2;
    grid-column: 2;
}

#italian-container {
    grid-row: 2;
    grid-column: 3;
}

#french-container {
    grid-row: 2;
    grid-column: 4;
}

#english-container {
    grid-row: 2;
    grid-column: 5;
}

/* DETAILS PAGE */

#details-body {
    display: flex;
    justify-content: center;
}

#details-body .h1-container{
    grid-row: 1 ;
    grid-column: 2;
}

.container-details {
    display: grid;
    background-color: rgb(66, 66, 66);
    justify-self: center;
    height: fit-content;
    width: fit-content;
}

.details-img-container {
    grid-row: 1;
    grid-column: 1;
    margin: 2vh;
    padding: 2vh;
    background-color: rgba(66, 66, 66);
    border-top: 1px solid rgba(0, 0, 0, 0.8);
    border-left: 1px solid rgba(0, 0, 0, 0.8);
    border-right: 5px solid rgba(0, 0, 0, 0.8);
    border-bottom: 3px solid rgba(0, 0, 0, 0.8);
}

.details-img-container img {
    width: 15vw;
    height: 30vh;
    border: 2px solid white;
}

.details-title-container {
    grid-row: 1;
    grid-column: 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin: 2vh;
    padding: 2vh;
    padding-left: 0;
    padding-bottom: 0;
}

.details-title-container h2 {
    margin: 0;
}

.info-container {
    grid-row: 2;
    grid-column: 2;
    padding: 3vh;
    width: 45vh;
    height: fit-content;
    padding: 2vh;
    margin: 2vh;
    color: white;
    background-color: rgba(66, 66, 66);
    border-top: 1px solid rgba(0, 0, 0, 0.8);
    border-left: 1px solid rgba(0, 0, 0, 0.8);
    border-right: 5px solid rgba(0, 0, 0, 0.8);
    border-bottom: 3px solid rgba(0, 0, 0, 0.8);
}

.info-container ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding-left: 0.5vw;
    font-weight: 500;
    line-height: 2.1vh;
}

.info-container li{
    padding: 1vh;
}

.text-container {
    grid-row: 3;
    grid-column: 2;
    padding: 2vh;
    margin: 2vh;
    width: auto;
    height: auto;
    font-size: 2.2vh;
    color: white;
    background-color: rgba(66, 66, 66);
    border-top: 1px solid rgba(0, 0, 0, 0.8);
    border-left: 1px solid rgba(0, 0, 0, 0.8);
    border-right: 5px solid rgba(0, 0, 0, 0.8);
    border-bottom: 3px solid rgba(0, 0, 0, 0.8);
}

.text-container ol {
    line-height: 3.4vh;
    padding-left: 2vw;
}

.ingredients-list {
    grid-row: 2;
    grid-column: 1;
    width: auto;
    height: fit-content;
    margin: 2vh;
    padding: 2vh;
    line-height: 3.4vh;
    color: white;
    background-color: rgba(66, 66, 66);
    border-top: 1px solid rgba(0, 0, 0, 0.8);
    border-left: 1px solid rgba(0, 0, 0, 0.8);
    border-right: 5px solid rgba(0, 0, 0, 0.8);
    border-bottom: 3px solid rgba(0, 0, 0, 0.8);
}

.ingredients-list ul {
    list-style: square;
    padding-left: 20px;
}

.video-container {
    background-color: white;
    grid-row: 4;
    grid-column: 2;
    margin: 2vh;
    padding: 2vh;
    background-color: rgba(66, 66, 66);
    border-top: 1px solid rgba(0, 0, 0, 0.8);
    border-left: 1px solid rgba(0, 0, 0, 0.8);
    border-right: 5px solid rgba(0, 0, 0, 0.8);
    border-bottom: 3px solid rgba(0, 0, 0, 0.8);
}

iframe {
    max-height: 40vh;
    max-width: 40vw;
    border: 1px solid white;
}


/* Mobile First (smartphone) */
@media only screen and (max-width: 600px) {

    body {
        margin: 2vh;
    }

    .nav-bar {
        display: flex;
        align-items: center;
    }

    .nav-bar a {
        padding: 10px;
        padding-left: 20px;
        padding-right: 20px;
        margin-bottom: 1vh;
    }

    input {
        width: 80vw;
    }

    .h1-container {
        display: flex;
        justify-content: center;
        width: 90vw;
    }

    h1 {
        text-align: center;
        font-size: 2.5vh;
    }

    h2 {
        grid-row: 1;
        grid-column: 1;
        width: inherit;
        text-align: center;
        font-size: 1.5em;
        padding: 10px;
    }

    h4 {
        margin: 0.5vh;
        padding-left: 1vh;
    }

    .mother-container {
        flex-direction: column;
        align-self: center;
        margin-right: 0vh;
        background-color: rgb(14, 57, 122, 0.5);
        border-radius: 5px;
        border-left: 5px solid rgb(14, 57, 122, 0.8);
        border-right: 5px solid rgb(14, 57, 122, 0.8);
    }

    .child-container {
        display: flex;
        flex-direction: column;
        padding: 10px;
        width: fit-content;
        grid-template-rows: 200px auto auto;
        min-width: 250px;
    }

    .title-container {
        display: grid;
        justify-content: center;
        gap: 10px;
    }

    .child-container img {
        width: 100%;
        height: auto;
    }

    .container-title-asian {
        grid-column: 1;
        grid-row: 1;
    }

    #asian-container {
        grid-column: 1;
        grid-row: 2;
    }

    .container-title-spanish {
        grid-column: 1;
        grid-row: 3;
    }

    #spanish-container {
        grid-column: 1;
        grid-row: 4;
    }

    .container-title-italian {
        grid-column: 1;
        grid-row: 5;
    }

    #italian-container {
        grid-column: 1;
        grid-row: 6;
    }

    .container-title-french {
        grid-column: 1;
        grid-row: 7;
    }

    #french-container {
        grid-column: 1;
        grid-row: 8;
    }

    .container-title-english {
        grid-column: 1;
        grid-row: 9;
    }

    #english-container {
        grid-column: 1;
        grid-row: 10;
    }

    /* DETAILS MEDIA QUERIES SMARTPHONE */
    .container-details {
        display: grid;
        grid-template-columns: 1fr;
        width: 90vw;
        margin: 1vh;
        padding: 1vh;
    }

    .details-img-container {
        grid-row: 1;
        grid-column: 1;
        margin: 1vh;
        padding: 1vh;
        width: auto;
    }

    .details-img-container img {
        width: 100%;
        height: auto;
        margin: 0;
        padding: 0;
        aspect-ratio: 3/3;
    }

    .details-title-container {
        grid-row: 2;
        grid-column: 1;
        margin: 1vh;
        padding: 1vh;
        width: auto;
    }

    .info-container {
        grid-row: 3;
        grid-column: 1;
        margin: 1vh;
        padding: 1vh;
        width: auto;
    }

    .ingredients-list {
        grid-row: 4;
        grid-column: 1;
        margin: 1vh;
        padding: 1vh;
        width: auto;
    }

    .ingredients-list ul {
        font-size: 1.5vh;
        padding-left: 10vw;
    }
  ol {
        font-size: 1.5vh;
        padding-left: 10vw;
    }

    .text-container {
        grid-row: 5;
        grid-column: 1;
        margin: 1vh;
        padding: 1vh;
        width: auto;
    }

    .video-container {
        grid-row: 6;
        grid-column: 1;
        margin: 1vh;
        padding: 1vh;
        width: auto;
    }

    iframe {
        width: 100%;
        height: auto;
        aspect-ratio: 16/9;
        max-width: none;
        max-height: none;
    }

}

/* Tablet styles */
@media only screen and (min-width: 601px) and (max-width: 1024px) {

    body {
        width: auto;
        margin-left: 2vw;
        margin-right: 2vw;
    }

    .nav-bar {
        flex-direction: row;
        justify-content: center;
        flex-wrap: wrap;
    }

    .nav-bar a {
        padding: 10px;
        padding-left: 30px;
        padding-right: 30px;
        margin: 1vh;
    }

    input {
        width: 70vw;
    }

    h1 {
        width: 100%;
        text-align: center;
        font-size: 3.5vh;
    }

    h2 {
        grid-row: 1;
        grid-column: 1;
        width: inherit;
        text-align: center;
        font-size: 1.5em;
        padding: 10px;
    }

    .mother-container {
        flex-direction: column;
        width: inherit;
        margin-right: 0vh;
        background-color: rgb(14, 57, 122, 0.5);
        border-radius: 5px;
        border-left: 5px solid rgb(14, 57, 122, 0.8);
        border-right: 5px solid rgb(14, 57, 122, 0.8);
    }

    .child-container {
        display: flex;
        flex-direction: column;
        grid-template-rows: 200px auto auto;
        min-width: auto;
        max-width: 100%;
        padding: 10px;
    }

    /* Use a two-column grid layout for larger screens */
    .title-container {
        display: grid;
        justify-content: center;
        grid-template-columns: repeat(2, 1fr);
        gap: 15px;
    }

    .child-container img {
        width: 100%;
        height: 280px;
    }

    .container-title-asian {
        grid-column: 1;
        grid-row: 1;
    }

    #asian-container {
        grid-column: 1;
        grid-row: 2;
    }

    .container-title-spanish {
        grid-column: 2;
        grid-row: 1;
    }

    #spanish-container {
        grid-column: 2;
        grid-row: 2;
    }

    .container-title-italian {
        grid-column: 1;
        grid-row: 3;
    }

    #italian-container {
        grid-column: 1;
        grid-row: 4;
    }

    .container-title-french {
        grid-column: 2;
        grid-row: 3;
    }

    #french-container {
        grid-column: 2;
        grid-row: 4;
    }

    .container-title-english {
        grid-column: 1;
        grid-row: 5;
    }

    #english-container {
        grid-column: 1;
        grid-row: 6;
    }

    /* Details page tablet layout */
    .container-details {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2vh;
        width: 95vw;
        margin: 2vh;
        padding: 2vh;
    }

    .details-img-container {
        grid-row: 2;
        grid-column: 1 / span 2;
        max-height: auto;
        max-width: auto;
        grid-template-columns: auto;
    }

    .details-img-container img {
        width: 100%;
        height: auto;
        aspect-ratio: 4/3;
    }

    .details-title-container {
        grid-row: 1;
        grid-column: 1 / span 2;
        margin: 2vh;
        justify-content: flex-start;
        height: fit-content;
    }

    .info-container {
        grid-row: 3;
        grid-column: 2;
        width: auto;
    }

    .ingredients-list {
        grid-row: 3;
        grid-column: 1;
        width: auto;
    }

    .text-container {
        grid-row: 4;
        grid-column: 1 / span 2;
    }

    .video-container {
        grid-row: 5;
        grid-column: 1 / span 2;
        width: auto;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    iframe {
        width: 100%;
        height: 50vh; 
        max-width: 800px; 
        aspect-ratio: 16/9; 
        margin: 0 auto; 
    }
}


/* HOVER STATES */

.nav-bar a:hover {
    color: white;
    padding: 10px;
    padding-left: 55px;
    padding-right: 55px;
    background-color: rgb(66, 66, 66);
    border-top: 1px solid white;
    border-left: 1px solid white;
    border-right: 5px solid white;
    border-bottom: 3px solid white;
    border-radius: 3px;
}

input:hover {
    background-color: rgb(66, 66, 66);
    border: none;
    width: 50vw;
    height: 4vh;
    margin-bottom: 2vh;
    border-radius: 3px;
    border-top: 1px solid white;
    border-left: 1px solid white;
    border-right: 1px solid white;
    border-bottom: 3px solid white;
}

input:focus {
    text-indent: 20px;
    outline: none;
    color: white;
    border-top: 1px solid white;
    border-left: 1px solid white;
    border-right: 1px solid white;
    border-bottom: 3px solid white;
}

input:focus::placeholder {
    color: transparent;
}

.child-container img:hover {
    background-color: none;
    border: 2px solid white;
}

a:hover {
    color: black;
    padding-top: 0.5vh;
    padding-left: 1vw;
    padding-right: 1vw;
    background-color: white;
}


